import React, { useEffect, useState } from "react";
import { calendar__bttom, noImage } from "../../../assets/images";
import { i18n } from "../../../i18n";
import Breadcrumb from "../Breadcrumb";
import Image from "../Image";
import "./DetaillWrapper.css";
import { useRouteMatch } from "react-router-dom";
import Translate from "../Translate";
import Date from "../Date";
import Empty from "../Nodatafound/Empty";
import { Link } from "react-router-dom";
import Pagination from "src/view/shared/table/Pagination";
import EvenementService from "src/modules/evenement/evenementService";

function Detaill() {
  const match = useRouteMatch();
  const [data, setData] = useState(new Array());
  const [count, setCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize, setPageSize] = useState(12);
  const [loading, setLoading] = useState(false);

  const langue = localStorage.getItem("language");
  let titleBreadcrumb =
    match.path === "/detaill/Evenement/Venir"
      ? "événement a venir "
      : "événement passé";

  const getData = async (offset) => {
    if (match.path === "/detaill/Evenement/Venir") {
      try {
        setLoading(true);
        let result = await EvenementService.evenementvenir(
          {},
          "",
          pageSize,
          offset
        );
        setData(result.rows);
        setCount(result.count);
        setLoading(false);
      } catch (error) {
        console.log("err", error);
        setLoading(false);
      }
    } else if (match.path === "/detaill/Evenement/Passe") {
      try {
        setLoading(true);
        let result = await EvenementService.evenementpasse(
          {},
          "",
          pageSize,
          offset
        );
        console.log("result", result);
        setData(result.rows);
        setCount(result.count);
        setLoading(false);
      } catch (error) {
        console.log("err", error);
        setLoading(false);
      }
    }
  };
  useEffect(() => {
    getData(0);
  }, []);
  return (
    <div className="app__detaill">
      {loading && (
        <h4 className="h4 detaill__sketlon">{i18n("table.loading")}</h4>
      )}
      {!loading && data.length <= 0 ? (
        <Empty />
      ) : (
        <>
          <div className="app__calendar">
            <Link to="/evenement">
              <img src={calendar__bttom} alt={noImage} />
            </Link>
          </div>
          <div className="__detaillwrapper">
            <Breadcrumb
              title={titleBreadcrumb}
              items={[
                [i18n("app.title"), "/"],
                [i18n(titleBreadcrumb), "/evenement"],
              ]}
            />
            <div className="detaill">
              <div className="list__detaills">
                {data.map((item) => (
                  <div>
                    <Link to={`/evenement/${item.id}`}>
                      <Image
                        src={
                          langue === "fr"
                            ? item.photos[0]?.downloadUrl
                            : langue === "ar"
                            ? item.photosAR[0]?.downloadUrl
                            : item.photosEN[0]?.downloadUrl
                        }
                        alt={noImage}
                        id="image__desktop"
                        width="370"
                        height="300"
                      />
                      <div className="list__detaill">
                        <div className="detaill__header">
                          <div className="header__left"></div>
                          <div className="header__right">
                            {Date.HourMinute(item.date)}
                          </div>
                        </div>{" "}
                        <div className="detaill__content text__wrap">
                          {Translate.Trans("title", item)}
                        </div>
                      </div>
                    </Link>
                  </div>
                ))}
              </div>
              <Pagination
                onChange={(e) => {
                  const offNumber = (e.current - 1) * e.pageSize;
                  getData(offNumber);
                  setCurrentPage(e.current);
                }}
                disabled={loading}
                pagination={{
                  current: currentPage,
                  pageSize: pageSize,
                  showSizeChanger: true,
                  total: count,
                }}
              />
            </div>
          </div>
        </>
      )}
    </div>
  );
}

export default Detaill;
